      this.vaxios.selectTimer = setTimeout(async () => {
        this.callWPApi(13, this.locationSelect.apiCall);
        this.callWPApi(14, this.zoneSelect.apiCall);
        const respo = await this.callWPApi(15, this.subzoneSelect.apiCall);
        if (respo) {
          this.checkActiveFilters();
        }
      }, 2500);
    },
    resetSearchTextField() {
      this.p_search_text = "";
      this.checkActiveFilters();
    },
    resetLocations() {
      this.p_location.provinces = [];
      this.p_location.locations = [];
      this.p_location.zones = [];
      this.p_location.subzones = [];
    },
    resetFeatures() {
      this.p_features.forEach((element) => {
        element.selected = false;
      });
    },
    resetAllFilters() {
      this.resetSearchTextField();
      this.resetPropertyType();
      this.resetSelectedRooms();
      this.resetSelectedBaths();
      this.resetSelectedSize();
      this.resetSelectedPrice();
      this.resetLocations();
      this.resetFeatures();
      this.menuController({}, null, true);
      this.checkActiveFilters();
    },
    checkActiveFilters() {
      this.front.searchingBar = true;
      if (this.p_search_text.length > 3) {
        this.vaxios.call_parameters.search_code = this.p_search_text;
      }
      if (this.typesActive) {
        let type_array = new Array();
        this.vaxios.initial_parameters.type.selected.forEach((element) => {
          type_array.push(element.id);
        });
        this.vaxios.call_parameters["type"] = type_array.join(",");
      } else if ("type" in this.vaxios.call_parameters) {
        delete this.vaxios.call_parameters.type;
      }
      if (this.p_rooms_value.length === 0) {
        delete this.vaxios.call_parameters.rooms;
      }
      if (this.p_baths_value.length === 0) {
        delete this.vaxios.call_parameters.bathrooms;
      }
      if (
        Number(this.p_size.min) !== Number(this.vaxios.threshold.size.min) ||
        Number(this.p_size.max) !== Number(this.vaxios.threshold.size.max)
      ) {
        this.vaxios.call_parameters["min_meters"] = this.p_size.min;
        this.vaxios.call_parameters["max_meters"] = this.p_size.max;
      } else {
        delete this.vaxios.call_parameters.min_meters;
        delete this.vaxios.call_parameters.max_meters;
      }
      if (this.p_price.max !== this.vaxios.threshold.price.max) {
        this.vaxios.call_parameters["max_price"] = this.p_price.max;
      } else {
        delete this.vaxios.call_parameters.max_price;
      }
      if (this.p_location.provinces.length > 0) {
        this.vaxios.call_parameters["province"] =
          this.p_location.provinces.join(",");
      } else {
        delete this.vaxios.call_parameters.province;
      }
      if (this.p_location.locations.length > 0) {
        this.vaxios.call_parameters["location"] =
          this.p_location.locations.join(",");
      } else {
        delete this.vaxios.call_parameters.location;
      }
      if (this.p_location.zones.length > 0) {
        this.vaxios.call_parameters["zone"] = this.p_location.zones.join(",");
      } else {
        delete this.vaxios.call_parameters.zone;
      }
      if (this.p_location.subzones.length > 0) {
        this.vaxios.call_parameters["subzone"] =
          this.p_location.subzones.join(",");
      } else {
        delete this.vaxios.call_parameters.subzone;
      }
      delete this.vaxios.call_parameters.featured;
      let filteredParams = this.vaxios.call_parameters;
      delete filteredParams.limit;
      this.getNumberOfProperties(2, filteredParams);
      //this.getOutstandingProperties(3, filteredParams);
      this.waitForCall(0, this.vaxios.call_parameters, true);
    },
    checkPropertyType() {
      this.vaxios.initial_parameters.type.selected = [];
      this.vaxios.types.forEach((element) => {
        if (element.selected) {
          this.vaxios.initial_parameters.type.selected.push(element);
        }
      });
      if (this.vaxios.initial_parameters.type.selected.length === 0) {
        this.vaxios.initial_parameters.type.empty = true;
      } else {
        this.vaxios.initial_parameters.type.empty = false;
      }
      this.checkActiveFilters();
    },
    resetPropertyType() {
      this.vaxios.types.forEach((element) => {
        element.selected = false;
      });
      this.vaxios.initial_parameters.type.selected = [];
      this.vaxios.initial_parameters.type.empty = true;
      this.checkActiveFilters();
    },

    checkSelectedRooms() {
      if (this.p_rooms_value.length > 0) {
        this.p_rooms_value.sort();
        var [min, max] = [
          Math.min(...this.p_rooms_value),
          Math.max(...this.p_rooms_value),
        ];

        var missingValues = () => {
          return new Promise((resolve) => {
            let missV = Array.from(Array(max - min), (v, i) => i + min).filter(
              (i) => !this.p_rooms_value.includes(i)
            );
            resolve(missV);
          });
        };
        missingValues().then((missV) => {
          if (missV.length === 1 && this.p_rooms_value.length > 1) {
            if (
              missV[0] < max &&
              missV[0] > min &&
              this.p_rooms_value_old.includes(missV[0])
            ) {
              this.p_rooms_value = [];
              this.p_rooms_value.push(missV[0]);
            } else {
              this.p_rooms_value = this.p_rooms_value.concat(missV);
            }
          } else {
            this.p_rooms_value = this.p_rooms_value.concat(missV);
          }

          this.p_rooms_selected = [];
          this.p_rooms_value.sort();
          this.p_rooms_value.forEach((element) => {
            this.p_rooms_selected.push(this.p_rooms[element]);
          });
          this.p_rooms_value_old = [...this.p_rooms_value];
          this.p_rooms_value_old.sort();
          if (this.p_rooms_value.length > 0) {
            let rooms_array = [];
            if (this.p_rooms_value.length === 1) {
              rooms_array.push(min + 1);
              if (max === 4) {
                for (var i = rooms_array[0]; i < 8; i++) {
                  rooms_array.push(i + 1);
                }
              }
              this.vaxios.call_parameters["rooms"] = rooms_array.join(",");
            } else {
              this.p_rooms_value.forEach((element) => {
                rooms_array.push(element + 1);
              });
              if (rooms_array.length === 5) {
                for (var j = 2; j < 15; j++) {
                  rooms_array.push(max + j);
                }
              }
              this.vaxios.call_parameters["rooms"] = rooms_array.join(",");
            }
          }
        });
      } else {
        this.p_rooms_value_old = [];
        this.p_rooms_selected = [];
      }

      this.checkActiveFilters();
    },

    resetSelectedRooms() {
      this.p_rooms_value = [];
      this.p_rooms_selected = [];
      this.checkActiveFilters();
    },

    checkSelectedBaths() {
      if (this.p_baths_value.length > 0) {
        this.p_baths_value.sort();
        var [min, max] = [
          Math.min(...this.p_baths_value),
          Math.max(...this.p_baths_value),
        ];

        var missingValues = () => {
          return new Promise((resolve) => {
            let missV = Array.from(Array(max - min), (v, i) => i + min).filter(
              (i) => !this.p_baths_value.includes(i)
            );
            resolve(missV);
          });
        };
        missingValues().then((missV) => {
          if (missV.length === 1 && this.p_baths_value.length > 1) {
            if (
              missV[0] < max &&
              missV[0] > min &&
              this.p_baths_value_old.includes(missV[0])
            ) {
              this.p_baths_value = [];
              this.p_baths_value.push(missV[0]);
            } else {
              this.p_baths_value = this.p_baths_value.concat(missV);
            }
          } else {
            this.p_baths_value = this.p_baths_value.concat(missV);
          }

          this.p_baths_selected = [];
          this.p_baths_value.sort();
          this.p_baths_value.forEach((element) => {
            this.p_baths_selected.push(this.p_baths[element]);
          });
          this.p_baths_value_old = [...this.p_baths_value];
          this.p_baths_value_old.sort();
          if (this.p_baths_value.length > 0) {
            let baths_array = [];
            if (this.p_baths_value.length === 1) {
              baths_array.push(min + 1);
              if (max === 2) {
                for (var i = baths_array[0]; i < 8; i++) {
                  baths_array.push(i + 1);
                }
              }
              this.vaxios.call_parameters["bathrooms"] = baths_array.join(",");
            } else {
              this.p_baths_value.forEach((element) => {
                baths_array.push(element + 1);
              });
              if (max === 2) {
                for (var j = 2; j < 8; j++) {
                  baths_array.push(max + j);
                }
              }
              this.vaxios.call_parameters["bathrooms"] = baths_array.join(",");
            }
          }
        });
      } else {
        this.p_baths_value_old = [];
        this.p_baths_selected = [];
      }
      this.checkActiveFilters();
    },

    resetSelectedBaths() {
      this.p_baths_value = [];
      this.p_baths_selected = [];
      this.checkActiveFilters();
    },

    checkSelectedSize() {
      this.checkActiveFilters();
    },

    resetSelectedSize() {
      this.p_size.min = this.vaxios.threshold.size.min;
      this.p_size.max = this.vaxios.threshold.size.max;
    },

    checkSelectedPrice() {
      this.checkActiveFilters();
    },

    resetSelectedPrice() {
      this.p_price.max = this.vaxios.threshold.price.max;
      this.checkActiveFilters();
    },

    checkSelectedFeature() {
      this.p_features.forEach((element) => {
        if (element.selected) {
          this.vaxios.call_parameters[element.id] = true;
        } else {
          delete this.vaxios.call_parameters[element.id];
        }
      });
      this.checkActiveFilters();
    },

    writeChips(
      title,
      array,
      isObject = false,
      parameter_name = new String(),
      separator = " | ",
      extension = ""
    ) {
      let newArray = [];
      array.forEach((element) => {
        let parameter_name_with_extension = new String();
        if (isObject) {
          parameter_name_with_extension = element[parameter_name] + extension;
        } else {
          parameter_name_with_extension = element + extension;
        }
        newArray.push(parameter_name_with_extension);
      });
      array = newArray;

      return title.toUpperCase() + ": " + array.join(separator);
    },

    operationTypeChange() {
      this.vaxios.searching = true;
      this.vaxios.threshold.sale.limit = false;
      this.vaxios.outstanding.propertys = [];
      this.vaxios.threshold.sale.outstanding = 0;
      this.vaxios.response.properties = [];
      this.parameters.operation.sale = !this.parameters.operation.sale;
      if (this.operation_toggle === 0) {
        this.vaxios.initial_parameters.operation = "v";
      } else {
        this.vaxios.initial_parameters.operation = "a";
      }
      this.callWPApi(11, {
        operation: this.vaxios.initial_parameters.operation,
        search_order_field: 4,
        search_order: "asc",
      });
      this.callWPApi(11, {
        operation: this.vaxios.initial_parameters.operation,
        search_order_field: 4,
        search_order: "desc",
      });
      this.callWPApi(11, {
        operation: this.vaxios.initial_parameters.operation,
        search_order_field: 3,
        search_order: "desc",
      });
      //Call for provinces
      this.callWPApi(12, {
        operation: this.vaxios.initial_parameters.operation,
      });
      //Call for locations
      this.callWPApi(13, {
        operation: this.vaxios.initial_parameters.operation,
      });
      //Call for zones
      this.callWPApi(14, {
        operation: this.vaxios.initial_parameters.operation,
      });
      //Call for subzones
      this.callWPApi(15, {
        operation: this.vaxios.initial_parameters.operation,
      });
      this.getProperties(this.firstTime);
    },

    waitForCall(ext, param = {}) {
      clearTimeout(this.vaxios.timer);
      this.vaxios.timer = setTimeout(() => {
        this.callWPApi(ext, param, true);
      }, 2500);
    },
    async callWPApi(ext, param = {}, firstTimeFromChange = false) {
      this.vaxios.searching = true;
      this.vaxios.response.list = {};
      var base_url = "https://inmobatehome.testing-robler.com";
      /* var base_url = window.location.origin; */
      var config = {
        method: "post",
        url: base_url + this.vaxios.api_relative_url,
        data: {
          extension: ext,
          parameters: param,
        },
      };
      /* let searching = this.vaxios.searching; */
      try {
        const response = await axios(config);
        if (ext === 0) {
          if (response.data.customer_properties.length > 0) {
            this.parseProperties(response.data, firstTimeFromChange);
            this.vaxios.propertySearchEmpty = false;
          } else {
            this.vaxios.response.properties = [];
            this.vaxios.propertySearchEmpty = true;
            this.front.searchingBar = false;
            this.vaxios.searching = false;
          }
        } else if (ext === 1) {
          this.lightbox.imgs = [];
          this.vaxios.single_response = response.data.propertys[0];
          response.data.propertys[0].propertyImages.forEach((element) => {
            this.lightbox.imgs.push({
              src: element.imageUrl,
              title: this.vaxios.single_response.propertyTitle.toUpperCase(),
            });
          });
          this.mortgageForm.values.saving =
            (Number(
              this.vaxios.single_response.propertyOperation.operationPriceSale
            ) *
              15) /
            100;
          this.mortgageForm.values.price =
            this.vaxios.single_response.propertyOperation.operationPriceSale -
            this.mortgageForm.values.saving;
          this.vaxios.single_property_loaded = true;
          this.vaxios.searching = false;
        } else if (ext === 2) {
          this.vaxios.threshold.sale.number = response.data.num_properties;
        } else if (ext == 3) {
          this.vaxios.outstanding.propertys = response.data.outstanding;
          this.vaxios.threshold.sale.outstanding =
            this.vaxios.outstanding.propertys.length;
        } else if (ext == 7) {
          this.vaxios.types = [];
          response.data.types.forEach((element) => {
            const parsedType = {
              id: element.id,
              text: element.value,
              selected: false,
            };
            this.vaxios.types.push(parsedType);
          });
        } else if (ext == 9) {
          this.callWPApi(10, param);
        } else if (ext == 10) {
          this.contact = false;
          this.contactForm.values.name = "";
          this.contactForm.values.surname = "";
          this.contactForm.values.email = "";
          this.contactForm.values.phone = "";
          this.contactForm.values.weekDays = [];
          this.contactForm.values.time = null;
          this.contactForm.sending = false;
          this.contactForm.sent = true;
        } else if (ext == 11) {
          if (param.search_order_field == 4) {
            // By size
            if (param.search_order == "asc") {
              this.vaxios.threshold.size.min = parseInt(
                response.data.customer_properties[0].propertyFeatures
                  .featuresAreaConstructed
              );
              if (this.vaxios.threshold.size.min < 0) {
                this.p_size.min = 0;
              } else {
                this.p_size.min = this.vaxios.threshold.size.min;
              }
            } else {
              this.vaxios.threshold.size.max = parseInt(
                response.data.customer_properties[0].propertyFeatures
                  .featuresAreaConstructed
              );
              this.p_size.max = this.vaxios.threshold.size.max;
            }
          } else if (param.search_order_field == 3) {
            // By price
            let max_price = 0;
            if (param.operation == "v") {
              max_price =
                response.data.customer_properties[0].propertyOperation
                  .operationPriceSale;
            } else {
              max_price = Number(
                response.data.customer_properties[0].propertyOperation
                  .operationPriceRent
              );
            }
            this.vaxios.threshold.price.max = Number(max_price);
            this.p_price.max = Number(this.vaxios.threshold.price.max);
          }
        } else if (ext == 12) {
          this.vaxios.location.provinces = response.data.provinces;
        } else if (ext == 13) {
          this.vaxios.location.locations = response.data.locations;
          let newSelectedLocations = [];
          this.vaxios.location.locations.forEach((element) => {
            if (this.p_location.locations.includes(element.id)) {
              newSelectedLocations.push(element.id);
            }
          });
          this.p_location.locations = newSelectedLocations;
        } else if (ext == 14) {
          this.vaxios.location.zones = response.data.zones;
          let newSelectedZones = [];
          this.vaxios.location.zones.forEach((element) => {
            if (this.p_location.zones.includes(element.id)) {
              newSelectedZones.push(element.id);
            }
          });
          this.p_location.zones = newSelectedZones;
        } else if (ext == 15) {
          this.vaxios.location.subzones = response.data.sub_zones;
          let newSelectedSubzones = [];
          this.vaxios.location.subzones.forEach((element) => {
            if (this.p_location.subzones.includes(element.id)) {
              newSelectedSubzones.push(element.id);
            }
          });
          this.p_location.subzones = newSelectedSubzones;
          this.vaxios.searchingBar = true;
          return true;
        }
      } catch (error) {
        console.error(error);
      }
    },
    getThis() {
      return this;
    },

    noMobileBreakpoint() {
      if (this.$vuetify.breakpoint.name !== "xs") {
        return true;
      } else {
        return false;
      }
    },
    menuController(on, filter = null, hideAll = false) {
      Object.entries(this.menu_container).forEach(([key]) => {
        this.menu_container[key] = false;
      });
      if (!hideAll) {
        setTimeout(() => {
          this.menu_container[filter] = on;
        }, 500);
      }
    },

    parseProperties(data, isNew = true) {
      if (isNew) {
        this.firstTime = false;
        if (this.vaxios.outstanding.propertys.length > 0) {
          this.vaxios.response.properties = [
            ...this.vaxios.outstanding.propertys,
          ].concat(data.customer_properties);
          this.vaxios.outstanding.propertys = [];
        } else {
          this.vaxios.response.properties = data.customer_properties;
        }
      } else {
        let counter = Object.keys(this.vaxios.response.properties).length;
        Object.entries(data.customer_properties).forEach(([key]) => {
          this.$set(
            this.vaxios.response.properties,
            counter,
            data.customer_properties[key]
          );
          counter++;
        });
      }
      this.front.searchingBar = false;
      this.vaxios.searching = false;
    },
    getNumberOfProperties(call_type, params) {
      this.callWPApi(call_type, params);
    },
    getOutstandingProperties(call_type, parameters) {
      this.callWPApi(call_type, parameters);
    },
    getPropertyTypes(call_type, operation_type) {
      let parameters = {
        operation: operation_type,
      };
      this.callWPApi(call_type, parameters);
    },
    getPropertyData(code) {
      this.vaxios.single_property_loaded = false;
      this.general.detail = true;
      this.general.detail_code = code;
      this.callWPApi(1, { property_code: this.general.detail_code });
    },
    orderProperties(order_by, order_type) {
      //order_by: (1=>Date, 2=>Rooms, 3=>Price, 4=>Meters, 5=>Bathrooms)
      //order_type: asc / desc
      this.vaxios.call_parameters["search_order_field"] = order_by;
      this.vaxios.call_parameters["search_order"] = order_type;
    },
    getOrderValues(order_by, order_type) {
      //order_by: (1=>Date, 2=>Rooms, 3=>Price, 4=>Meters, 5=>Bathrooms)
      //order_type: asc / desc
      this.vaxios.call_parameters["search_order_field"] = order_by;
      this.vaxios.call_parameters["search_order"] = order_type;
      this.callWPApi(11, this.vaxios.call_parameters);
    },
    scrollPropertySum() {
      let elem_num = this.vaxios.initial_parameters.element_number;
      if (
        Object.keys(this.vaxios.response.properties).length <
        this.vaxios.threshold.sale.number
      ) {
        elem_num.first = Object.keys(this.vaxios.response.properties).length;
        if (
          elem_num.first + elem_num.last >
          this.vaxios.threshold.sale.number
        ) {
          this.vaxios.threshold.sale.limit = true;
          elem_num.last =
            this.vaxios.threshold.sale.number -
            Object.keys(this.vaxios.response.properties).length;
        } else {
          this.vaxios.threshold.sale.limit = false;
        }
      }
      this.callWPApi(0, {
        operation: this.vaxios.initial_parameters.operation,
        limit:
          this.vaxios.initial_parameters.element_number.first -
          this.vaxios.threshold.sale.outstanding +
          "," +
          this.vaxios.initial_parameters.element_number.last,
        featured: "false",
      });
    },
    fixPropertyAddress(address) {
      let newAddress = new String();
      let zoneOrSubzone = false;
      if (address.addressZone != null) {
        newAddress = address.addressZone;
        if (address.addressSubZone != null) {
          newAddress = newAddress + " (" + address.addressSubZone + ")";
        }
        zoneOrSubzone = true;
      }
      if (address.addressTown != null) {
        if (zoneOrSubzone) {
          newAddress = newAddress + " - " + address.addressTown;
        } else {
          newAddress = address.addressTown;
        }
      }

      return newAddress;
    },
    fixPropertyConstructedArea(features) {
      let construc_area = new String();
      if (features.featuresAreaConstructed > 0) {
        if (
          features.featuresAreaUsable > 0 &&
          features.featuresAreaUsable != features.featuresAreaConstructed
        ) {
          construc_area = " / ";
        }
        return construc_area + features.featuresAreaConstructed + " m² ";
      } else {
        return new String();
      }
    },
    fixPropertyUsableArea(features) {
      if (
        features.featuresAreaUsable > 0 &&
        features.featuresAreaUsable != features.featuresAreaConstructed
      ) {
        return features.featuresAreaUsable + " m² ";
      } else {
        return new String();
      }
    },
    fixPropertyRooms(features) {
      let rooms = features.featuresRooms;
      /*  let iconroom = "<v-icon class='mr-2'>mdi-bed-king-outline</v-icon>"
      let iconbath = "<v-icon class='mr-2'>mdi-toilet</v-icon>" */
      let string = new String();

      if (rooms > 0) {
        string = "<b>" + rooms + "</b>";
      }
      return string;
    },
    fixSinglePropertyRooms(features) {
      let rooms = features.featuresRooms;
      /*  let iconroom = "<v-icon class='mr-2'>mdi-bed-king-outline</v-icon>"
      let iconbath = "<v-icon class='mr-2'>mdi-toilet</v-icon>" */
      let string = new String();

      if (rooms > 0) {
        string = rooms + " HAB.";
      }
      return string;
    },
    fixPropertyBaths(features) {
      let baths = features.featuresBathroomNumber;
      let string = new String();
      if (baths > 0) {
        string = "<b>" + baths + "</b>";
      }
      return string;
    },
    fixSinglePropertyBaths(features) {
      let baths = features.featuresBathroomNumber;
      let string = new String();
      if (baths > 0) {
        string = baths + " BAÑ.";
      }
      return string;
    },
    checkUrl(path) {
      const arrayPath = path.split("/").filter(String);
      const value = arrayPath[arrayPath.length - 1];
      return value;
    },
    createDetailUrl(code) {
      console.log('cambia');
      if (history.pushState) {
        console.log('cambia 2');
        var newurl =
          window.location.protocol +
          "//" +
          window.location.host +
          window.location.pathname +
          code;
        window.history.pushState({ path: newurl }, "", newurl);
        let uC = document.querySelectorAll("link[rel='canonical']")[0];
        uC.setAttribute("href", newurl);
      }
      this.getPropertyData(code);
    },
    checkPropertyFeature(prop) {
      return this.vaxios.single_response.propertyFeatures[prop];
    },
    propertyFeatureDict(prop) {
      let prop_dict = {
        featuresAccess: ["Accesibilidad", "mdi-wheelchair-accessibility", null],
        featuresAntenna: ["Parabólica", "mdi-antenna", null],
        featuresBalcony: ["Balcón", "mdi-balcony", null],
        featuresBuiltYear: ["Año de construcción", "mdi-check", null],
        featuresBus: ["Autobús", "mdi-bus-stop", null],
        featuresClub: ["Club", "mdi-cards-club", null],
        featuresCommunityParking: [
          "Aparcamiento comunitario",
          "mdi-parking",
          null,
        ],
        featuresConditionedAir: [
          "Aire acondicionado",
          "mdi-air-conditioner",
          null,
        ],
        featuresConservation: ["Nivel de estado general", "mdi-check", null],
        featuresDoorman: ["Portero", "mdi-face-man-outline", null],
        featuresElevator: ["Ascensor", "mdi-elevator-passenger", null],
        featuresElevatorGroundFloor: [
          "Ascensor (Planta baja)",
          "mdi-elevator-passenger",
          "featuresElevator",
        ],
        featuresEnergyCertificatePerfomance: [
          "Valor de rendimiento energético",
          "mdi-check",
          null,
        ],
        featuresEnergyCertificateRating: [
          "Nivel Certifiación energética",
          "mdi-check",
          null,
        ],
        featuresFloors: ["Alturas", "mdi-check", null],
        featuresFurnished: ["Amueblado", "mdi-table-furniture", null],
        featuresGames: ["Sala de juegos", "mdi-gamepad-variant", null],
        featuresGarage: ["Garaje", "mdi-garage-variant", null],
        featuresGarden: ["Jardín", "mdi-flower-outline", null],
        featuresGym: ["Gimnasio", "mdi-dumbbell", null],
        featuresHeating: ["Calefacción", "mdi-thermometer-plus", null],
        featuresHeatingName: ["Tipo de calefacción", "mdi-check", null],
        featuresHotWater: ["Agua Caliente", "mdi-water-check", null],
        featuresHotWaterName: ["Tipo de calentador de Agua", "mdi-check", null],
        featuresPool: ["Piscina", "mdi-pool", null],
        featuresSolarPanels: ["Paneles solares", "mdi-solar-panel", null],
        featuresSolarium: ["Solarium", "mdi-sunglasses", null],
        featuresStorage: ["Trastero", "mdi-archive", null],
        featuresTennis: ["Cancha de tenis", "mdi-tennis", null],
        featuresTerrace: ["Terraza", "mdi-fence", null],
        featuresVPO: ["Vivienda de protección oficial", "mdi-home-group", null],
        featuresVideoIntercom: ["Videoportero", "mdi-video-outline", null],
        featuresWallsName: ["Estado estructural", "mdi-check", null],
        featuresWardrobes: ["Armarios", "mdi-wardrobe-outline", null],
        featuresWindowsname: [
          "Estado de las ventanas",
          "mdi-window-closed-variant",
          null,
        ],
        featuresOrientationEast: ["Este", "mdi-check", null],
        featuresOrientationNorth: ["Norte", "mdi-check", null],
        featuresOrientationSouth: ["Sur", "mdi-check", null],
        featuresOrientationWest: ["Oeste", "mdi-check", null],
        featuresSeaViews: ["Vistas al mar", "mdi-lighthouse", null],
        featuresParkingAvailable: ["Parking exterior", "mdi-parking", null],
      };
      if (prop in prop_dict) {
        return prop_dict[prop];
      } else {
        return 0;
      }
    },
    returnFeatures() {
      let featuresObject = this.vaxios.single_response.propertyFeatures;
      let features = {};
      let bannedFeatures = [];
      Object.entries(featuresObject).forEach(([key]) => {
        const valueToString = String(featuresObject[key]);
        if (valueToString === "true") {
          if (
            key !== "featuresOrientationEast" &&
            key !== "featuresOrientationNorth" &&
            key !== "featuresOrientationSouth" &&
            key !== "featuresOrientationWest"
          ) {
            const featureTraduction = this.propertyFeatureDict(key);
            if (featureTraduction !== 0) {
              if (featureTraduction[2] !== null) {
                bannedFeatures.push(featureTraduction[2]);
              }
              if (!(key in features)) {
                features[key] = featureTraduction;
              }
            } else {
              console.error("Is new feature: ", key);
            }
          }
        }
      });
      bannedFeatures.forEach((element) => {
        delete features[element];
      });
      return features;
    },
    backToListing() {
      if (this.general.base_url === "") {
        let urlArray = window.location.href.split("/").filter((a) => a);
        urlArray.pop();
        urlArray.shift();
        let url = urlArray.join("/");
        url = "https://" + url;
        this.general.base_url = url;
      }
      if (history.pushState) {
        window.history.pushState(
          { path: this.general.base_url },
          "",
          this.general.base_url
        );
      }
      this.vaxios.response.properties = [];
      this.contactForm.sent = false;
      this.callWPApi(0, this.vaxios.call_parameters);
      this.general.detail = false;
    },
    sendContactForm() {
      const property = this.vaxios.single_response;
      let notes = new String();
      if (this.contactForm.values.weekDays.length > 0) {
        notes = "Días de contacto: ";
        this.contactForm.values.weekDays.forEach((element) => {
          notes = notes + element + ", ";
        });
      }
      if (this.contactForm.values.time !== null) {
        notes = notes + "Hora de contacto: " + this.contactForm.values.time;
      }
      const parameters = {
        property_id: property.propertyCode,
        contact_name: this.contactForm.values.name,
        contact_surname: this.contactForm.values.surname,
        contact_phone: this.contactForm.values.phone,
        contact_email: this.contactForm.values.email,
        contact_origin: "atehome.testing-robler.com",
        contact_notes: notes,
        contact_in_out: "IN",
      };

      if (
        /^\b([A-ZÀ-ÿ][-,a-z. ']+[ ]*)+$/.test(this.contactForm.values.name) &&
        /^\b([A-ZÀ-ÿ][-,a-z. ']+[ ]*)+$/.test(
          this.contactForm.values.surname
        ) &&
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
          this.contactForm.values.email
        ) &&
        /^\s*[+-]?(\d+|\.\d+|\d+\.\d+|\d+\.)(e[+-]?\d+)?\s*$/.test(
          this.contactForm.values.phone
        )
      ) {
        this.contactForm.sending = true;
        this.callWPApi(9, parameters);
      } else {
        console.error("Error de envio");
      }
    },
    mortgageSavingChange() {
      this.mortgageForm.values.price =
        Number(
          this.vaxios.single_response.propertyOperation.operationPriceSale
        ) - Number(this.mortgageForm.values.saving);
      this.mortgagePercentageText();
    },
    mortgagePriceChange() {
      this.mortgageForm.values.saving =
        Number(
          this.vaxios.single_response.propertyOperation.operationPriceSale
        ) - Number(this.mortgageForm.values.price);
      this.mortgagePercentageText();
    },
    mortgagePercentageText() {
      this.mortgageForm.values.savingPercent =
        Math.floor(
          (100 * Number(this.mortgageForm.values.saving)) /
            Number(
              this.vaxios.single_response.propertyOperation.operationPriceSale
            )
        ) + "% |";
    },
    mortgageNotaryPrice() {
      return (
        (Number(
          this.vaxios.single_response.propertyOperation.operationPriceSale
        ) *
          0.5) /
        100
      );
    },
    mortgageRegistryPrice() {
      return 500;
    },
    mortgageAgencyPrice() {
      return 375.1;
    },
    mortgageAppraisalPrice() {
      const price = Number(
        this.vaxios.single_response.propertyOperation.operationPriceSale
      );
      if (price >= 800000) {
        return 800;
      } else if (price < 800000 && price >= 300000) {
        return 600;
      } else {
        return 400;
      }
    },
    mortgagePropertyTypePrice() {
      if (this.mortgageForm.values.property_type === "new") {
        return (
          Number(
            this.vaxios.single_response.propertyOperation.operationPriceSale
          ) * 0.1
        );
      } else {
        return (
          Number(
            this.vaxios.single_response.propertyOperation.operationPriceSale
          ) * 0.04
        );
      }
    },
    mortgageInterestCalc() {
      /*
       * ir   - interest rate per month
       * np   - number of periods (months)
       * pv   - present value
       * fv   - future value
       * type - when the payments are due:
       *        0: end of the period, e.g. end of month (default)
       *        1: beginning of period
       */
      let ir = Number(this.mortgageForm.values.percent) / 100 / 12;
      let fv = 0;
      let type = 0;
      var pmt, pvif;
      let np = Number(this.mortgageForm.values.years) * 12;
      let pv =
        Number(this.mortgageForm.values.price) +
        this.mortgagePropertyTypePrice() +
        this.mortgageAgencyPrice() +
        this.mortgageAppraisalPrice() +
        this.mortgageNotaryPrice() +
        this.mortgageRegistryPrice();
      fv || (fv = 0);
      type || (type = 0);

      if (ir === 0) return -(pv + fv) / np;

      pvif = Math.pow(1 + ir, np);
      pmt = (-ir * (pv * pvif + fv)) / (pvif - 1);

      if (type === 1) pmt /= 1 + ir;

      return pmt;
    },
    mortgageTotalAmmountWithInterest() {
      return (
        -this.mortgageInterestCalc() *
        Number(this.mortgageForm.values.years) *
        12
      );
    },
    mortgageTotalAmmountWithInterestAndSaving() {
      return (
        -this.mortgageInterestCalc() *
          Number(this.mortgageForm.values.years) *
          12 +
        this.mortgageForm.values.saving
      );
    },
    mortgageTotalInterest() {
      return (
        this.mortgageTotalAmmountWithInterest() -
        (Number(this.mortgageForm.values.price) +
          this.mortgagePropertyTypePrice() +
          this.mortgageAgencyPrice() +
          this.mortgageAppraisalPrice() +
          this.mortgageNotaryPrice() +
          this.mortgageRegistryPrice())
      );
    },
    mortgageBaseCost() {
      return (
        Number(
          this.vaxios.single_response.propertyOperation.operationPriceSale
        ) +
        this.mortgageAppraisalPrice() +
        this.mortgageAgencyPrice() +
        this.mortgagePropertyTypePrice() +
        800
      );
    },
    facebookLinterMetatags() {
      document
        .querySelector('link[rel="canonical"]')
        .setAttribute("href", "https://hola.com");
      var meta = document.createElement("meta");
      meta.setAttribute("property", "og:url");
      meta.content = window.location.href;
      document.getElementsByTagName("head")[0].appendChild(meta);
    },
    // Call properties
    getProperties(firstTime = false) {
      const initial = this.vaxios.initial_parameters;
      this.getPropertyTypes(7, initial.operation);
      this.getNumberOfProperties(2, { operation: initial.operation });
      this.getOutstandingProperties(3, initial);
      this.vaxios.call_parameters = {
        operation: initial.operation,
        limit: initial.element_number.first + "," + initial.element_number.last,
        featured: "false",
      };
      this.callWPApi(0, this.vaxios.call_parameters, firstTime);
    },
  },
  beforeMount: function () {
    //this.facebookLinterMetatags();
    const lastpart = this.checkUrl(window.location.href);
    const regex = /^\d+$/;
    if (regex.test(lastpart)) {
      this.general.detail = true;
      /* this.general.detail_code = lastpart; */
      this.getPropertyData(lastpart);
    } else {
      this.general.base_url = window.location.href;
      this.general.detail = false;
    }
    /* if(this.noMobileBreakpoint()) {
      this.chartOptions.xaxis.tickAmount = 5;
    } else {
      this.chartOptions.xaxis.tickAmount = 2;
    } */
  },
  mounted() {
    const newThis = this;
    window.onpopstate = function (event) {
      if (event.type === "popstate") {
        //this.facebookLinterMetatags();
        newThis.vaxios.single_property_loaded = false;
        newThis.general.detail = false;
        newThis.vaxios.response.properties = [];
        newThis.operationTypeChange();
      }
    };
    this.operation_toggle = 0;
  },
  computed: {
    changeXaxis: function () {
      return ["xs"].includes(this.$vuetify.breakpoint.name) ? 2 : 5;
    },
    range: {
      get() {
        return [this.p_size.min, this.p_size.max];
      },
      set(val) {
        this.p_size.min = val[0];
        this.p_size.max = val[1];
      },
    },
    progress() {
      return (
        (this.mortgageForm.values.price * 100) /
        this.vaxios.single_response.propertyOperation.operationPriceSale
      );
    },
    color() {
      return ["success", "warning", "warning"][Math.floor(this.progress / 40)];
    },
    progress2() {
      return (
        (this.mortgageForm.values.saving * 100) /
        this.vaxios.single_response.propertyOperation.operationPriceSale
      );
    },
    color2() {
      return ["warning", "success", "success"][Math.floor(this.progress2 / 40)];
    },
    series() {
      let apexchartdata = [];
      apexchartdata.push({
        name: "Precio del inmueble",
        data: [
          Number(
            this.vaxios.single_response.propertyOperation.operationPriceSale
          ),
        ],
      });
      apexchartdata.push({
        name: "Impuestos y gastos",
        data: [
          Number(
            this.mortgageAppraisalPrice() +
              this.mortgageAgencyPrice() +
              this.mortgagePropertyTypePrice() +
              800
          ),
        ],
      });
      apexchartdata.push({
        name: "Ahorro aportado",
        data: [0, Number(this.mortgageForm.values.saving)],
      });
      apexchartdata.push({
        name: "Importe Hipoteca",
        data: [
          0,
          Number(this.mortgageForm.values.price) +
            this.mortgagePropertyTypePrice() +
            this.mortgageAgencyPrice() +
            this.mortgageAppraisalPrice() +
            this.mortgageNotaryPrice() +
            this.mortgageRegistryPrice(),
        ],
      });
      apexchartdata.push({
        name: "Interés Hipoteca",
        data: [0, this.mortgageTotalInterest()],
      });
      return apexchartdata;
    },
    provinceSelect() {
      return {
        value: this.p_location.provinces.join(","),
        active: this.p_location.provinces.length > 0,
      };
    },
    locationSelect() {
      let obj = {
        value: this.p_location.locations.join(","),
        active: this.p_location.locations.length > 0,
        apiCall: {},
      };
      if (this.provinceSelect.active) {
        obj.apiCall = {
          operation: this.vaxios.call_parameters.operation,
          province: this.provinceSelect.value,
        };
      }
      return obj;
    },
    zoneSelect() {
      let obj = {
        value: this.p_location.zones.join(","),
        active: this.p_location.zones.length > 0,
        apiCall: {},
      };
      if (this.provinceSelect.active) {
        obj.apiCall.province = this.provinceSelect.value;
      }
      if (this.locationSelect.active) {
        obj.apiCall.location = this.locationSelect.value;
      }
      obj.apiCall.operation = this.vaxios.call_parameters.operation;
      return obj;
    },
    subzoneSelect() {
      let obj = {
        value: this.p_location.subzones.join(","),
        active: this.p_location.subzones.length > 0,
        apiCall: {},
      };
      if (this.provinceSelect.active) {
        obj.apiCall.province = this.provinceSelect.value;
      }
      if (this.locationSelect.active) {
        obj.apiCall.location = this.locationSelect.value;
      }
      if (this.zoneSelect.active) {
        obj.apiCall.zone = this.zoneSelect.value;
      }
      obj.apiCall.operation = this.vaxios.call_parameters.operation;
      return obj;
    },
    locationsActive() {
      return (
        this.subzoneSelect.active ||
        this.zoneSelect.active ||
        this.locationSelect.active ||
        this.provinceSelect.active
      );
    },
    featuresActive() {
      let fa = false;
      this.p_features.forEach((element) => {
        if (element.selected) {
          fa = true;
        }
      });
      return fa;
    },
    typesActive() {
      return this.vaxios.initial_parameters.type.selected.length > 0;
    },
    roomsActive() {
      return this.p_rooms_selected.length > 0;
    },
    bathsActive() {
      return this.p_baths_value.length > 0;
    },
    sizeActive() {
      return (
        Number(this.p_size.min) !== Number(this.vaxios.threshold.size.min) ||
        Number(this.p_size.max) !== Number(this.vaxios.threshold.size.max)
      );
    },
    priceActive() {
      return this.p_price.max !== this.vaxios.threshold.price.max;
    },
    textActive() {
      return this.p_search_text.length > 0;
    },
    filtersActive() {
      return (
        this.textActive ||
        this.roomsActive ||
        this.typesActive ||
        this.bathsActive ||
        this.sizeActive ||
        this.priceActive ||
        this.locationsActive ||
        this.featuresActive
      );
    },
  },
  watch: {
    $route: function (to, from) {
      const canonical = document.querySelector('link[rel="canonical"]');
      if (canonical !== null) {
        canonical.href = window.location.href;
      }
      console.log("url cambia", to, from, window.location.href);
    },
    changeXaxis: function (val) {
      this.chartOptions.visualize = false;
      this.chartOptions.xaxis.tickAmount = val;
      this.chartOptions.visualize = true;
    },
    p_search_text: function () {
      if (this.p_search_text.length > 3) {
        if (Number.isInteger(Number(this.p_search_text))) {
          this.checkActiveFilters();
        } else {
          console.log("no es entero");
        }
      }
    },
  },
};
